.statistics {
  display: inline-block;
  width: 400px;
  padding: 20px;
  text-align: center;
  border: 2px solid rgb(27, 129, 44);
  transition: all 0.3s ease 0s;
  margin-left: 50px;
  margin-top: 50px;
}

.statistics:hover {
  box-shadow: 7px 7px 7px rgba(213, 211, 224, 0.829);
}

.title {
  font-size: 30px;
  color: rgb(27, 129, 44);
}

.statList {
  display: flex;
  justify-content: center;
  padding-left: 0;
}

.item {
  background-color: rgba(121, 245, 197, 0.829);
  display: flex;
  flex-direction: column;
  width: 80px;
  border: 1px solid rgb(27, 129, 44);
}

.label {
  color: rgb(34, 34, 100);
  font-weight: bold;
  font-size: 15px;
}

.percentage {
  color: rgb(34, 34, 100);
  font-size: 15px;
}
